::selection{
  background: rgba(26,188,156,0.3);
}
.login{
  background-color: rgba(241, 243, 247, 0.13);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login .wrapper{
  max-width: 440px;
  /* padding: 0 20px; */
  /* margin: 170px auto; */
  width: 100%;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 4px 10px 1px rgba(0,0,0,0.1);
  padding-top: 20px;
}
.wrapper .title{
  height: 90px;
  background: white;
  border-radius: 5px 5px 0 0;
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper form{
  padding: 30px 25px 25px 25px;
}
.wrapper form .row{
  height: 45px;
  margin-bottom: 15px;
  position: relative;
}
.errorContainer{
  margin-bottom: 15px;

}
.wrapper form .row input{
  height: 100%;
  width: 100%;
  outline: none;
  padding-left: 60px;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-size: 16px;
  transition: all 0.3s ease;
}
form .row input:focus{
  border-color: #11a7e2;
  box-shadow: inset 0px 0px 2px 2px rgba(26,188,156,0.25);
}
form .row input::placeholder{
  color: #999;
}
.wrapper form .row i{
  position: absolute;
  width: 47px;
  height: 100%;
  color: #fff;
  font-size: 18px;
  background: #11a7e2;
  border: 1px solid #11a7e2;
  border-radius: 5px 0 0 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper form .pass{
  margin: -8px 0 20px 0;
}
.wrapper form .pass a{
  color: #11a7e2;
  font-size: 17px;
  text-decoration: none;
}
.wrapper form .pass a:hover{
  text-decoration: underline;
}
.wrapper form .button input{
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  padding-left: 0px;
  background: #11a7e2;
  border: 1px solid #11a7e2;
  cursor: pointer;
}
form .button input:hover{
  background: #11a7e2;
}
.wrapper form .signup-link{
  text-align: center;
  margin-top: 20px;
  font-size: 17px;
}
.wrapper form .signup-link a{
  color: #11a7e2;
  text-decoration: none;
}
form .signup-link a:hover{
  text-decoration: underline;
}


.login .errorUser{
  color: red;
}

